import test from 'ava';

var java = require("java");
java.classpath.push("class");
java.classpath.push("gson-2.8.0.jar");
var Main = java.import('com.bazaarvoice.Main');
var main = new Main();



test.serial('foo', t => {

    java.callMethod(main, "searchResults", "text", "sample", function(err, result){
        t.is(result,3);
    });

});


test.serial('Soo', r => {

    java.callMethod(main, "searchResults", "word", "green", function(err, result){
        r.is(result,3);
    });

});